import sign from 'pages/user/sign'
export default {
	components: {
		sign
	},
	data() {
		return {
			/* 模糊窗口 */
			modalName: null
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/* 用户授权 */
		getUserInfo() {
			uni.login({
				provider: 'weixin',
				success: data => {
					console.log(data.code);
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: data1 => {
							console.log(data1.userInfo);
						}
					})
				}
			})
		},
		/* 模糊窗口 */
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
			setTimeout(() => {
				uni.showToast({
					title: '签到成功',
				});
			}, 300);
		},
		hideModal(e) {
			this.modalName = null
		},
		/* 页面跳转 */
		jump2formUser() {
			uni.navigateTo({
				url: '/pages/form/user/index'
			})
		},
		jump2formAddress() {
			uni.navigateTo({
				url: '/pages/list/address/index'
			})
		},
		jump2credit() {
			uni.navigateTo({
				url: '/pages/credit/index'
			})
		},
		jump2listOrder(e) {
			uni.navigateTo({
				url: '/pages/list/order/index?type=' + e
			})
		},
		jump2listOrderComment() {
			uni.navigateTo({
				url: '/pages/list/order/comment'
			})
		},
		jump2listOrderService() {
			uni.navigateTo({
				url: '/pages/list/order/service'
			})
		},
		jump2listUserFavorite() {
			uni.navigateTo({
				url: '/pages/list/user/favorite'
			})
		},
		jump2listUserHistory() {
			uni.navigateTo({
				url: '/pages/list/user/history'
			})
		},
		jump2listUserSubcribe() {
			uni.navigateTo({
				url: '/pages/list/user/subcribe'
			})
		},
		jump2ContentRichText() {
			uni.navigateTo({
				url: '/pages/content/rich-text/index'
			})
		},
	}
}
